.homeContainer {
  display: flex;
  margin-top: 2rem;
}
.leftContainer {
  display: flex;
  flex-direction: column;
  flex: 0.75;
  overflow: hidden;
}
.rightContainer {
  display: flex;
  flex-direction: column;
  flex: 0.25;
  overflow: hidden;
}

.postFlex {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}
.socialMedia {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}
.SocialMDButton {
  height: 3rem;
  outline: none;
  padding: 1rem 2rem;
  border-radius: 10px;
  display: flex;
  text-decoration: none;
  border: none;
  outline: none;
  align-items: center;
  margin-bottom: 0.5rem;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  font-size: 13px;
}

.facebook {
  background: linear-gradient(to bottom, #123dd7 0%, #062db9 100%);
  color: white;
}

.twitter {
  background: linear-gradient(to bottom, #000000 0%, #212224 100%);
  color: white;
}

.insta {
  background-image: linear-gradient(
    158deg,
    #863fa6 22%,
    #e12c61 50%,
    #f46f46 80%
  );
  color: white;
}

.youtube {
  background: linear-gradient(to bottom, #d71212 0%, #c5402f 100%);
  color: white;
}

.linked {
  background: linear-gradient(to bottom, #39b7ff 0%, #20a1eb 100%);
  color: white;
}
.categories {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 1rem;
}

.categoriesFlex {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.categories h6 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.socialMedia h6 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.latestContents {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
.latestContents h5 {
  font-size: 1rem;
  margin-bottom: 1rem;
}
.latest {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.random {
  display: flex;
  flex-basis: 48%;
  flex-direction: column;
  overflow: hidden;
  padding: 0rem 2rem;
}

.randomItem {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-radius: 5px;
  border: 1px solid whitesmoke;
}

.rdItem {
  width: 100%;
  padding: 1rem;
  border-bottom: 1px solid gainsboro;
}
.rdItem h6 {
  cursor: pointer;
}
.rdItem:hover h6 {
  text-decoration: underline;
}

