@media screen and (min-width: 700px) and (max-width: 1050px) {
  .homeContainer {
    flex-direction: column;
  }
}

@media screen and (max-width: 700px) {
  .homeContainer {
    flex-direction: column;
  }
  .postFlex {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .latest {
    flex-wrap: nowrap;
    flex-direction: column;
  }
}

@media screen and (max-width: 550px) {
  .homeContainer {
    flex-direction: column;
  }

  .postFlex {
    flex-direction: column;
    flex-wrap: nowrap;
  }
  .latest {
    flex-wrap: nowrap;
    flex-direction: column;
  }
}
