.card {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* flex-basis: 20%; */
  width: 100%;
  padding: 2rem 0.5rem;
  color: gray;
  margin-right: 1rem;
  cursor: pointer;
}

.cardText {
  width: 100%;
  display: flex;
  justify-content: left;
  padding: 1rem 0;
}

.card h3 {
  color: #000;
  font-size: 14px;
}
.cardText span {
  font-size: 12px;
}

.card:hover h3 {
  text-decoration: underline;
}
